.grid-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.grid-row {
    margin-left: 4vw;
    display: flex;
    flex-direction: row;
    align-items: center;
}

.buttons-group {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1vh 1vw;
}